import Vue from 'vue'
import Router from 'vue-router'
import Login from "../components/Login";
import AppIndex from "../home/AppIndex";

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'AppIndex',
      component: AppIndex
    }
  ]
})



